@import './colors';

.ImageSection{
    display: flex;
    margin-top: 4rem;
    margin-bottom: 8rem;
    .img{
        width: 100%;
        height: 39.4vh;
        img{
            width: 100%;
            object-fit: cover;
            height: 100%;
        }
    }

    .about-info{
        margin-left: 2rem;
        h4{
            font-size: 2rem;
        }
        .about-text{
            padding: 1rem 0;
        }
    }
    @media screen and (max-width: 1400px){
        flex-direction: column;
        .about-info{
            margin-left: 0;
            margin-top: 1rem;
        }
    }
}

.about-details{
    display: flex;
    p{
        padding: .35rem 0;
    }
    .right-section{
        margin-left: 2.5rem;
    }
}
